"use client";

import { useState } from "react";
import { Switch } from "~/components/ui/switch";
import { useDarkMode } from "~/hooks/useDarkMode";
import { InputSection } from "~/components/domains/Home/InputSection";
import { SummarySection } from "~/components/domains/Home/SummarySection";

export default function Home() {
  const { isDark, toggleTheme } = useDarkMode();
  const [text, setText] = useState("");
  const [response, setResponse] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async () => {
    if (!text.trim()) return;

    try {
      setIsLoading(true);
      const res = await fetch("/api/summary", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ text }),
      });

      const data = await res.json();
      setResponse(data.message);
    } catch (error) {
      console.error("Error:", error);
      setResponse("Ocurrió un error al procesar tu solicitud.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-white dark:bg-slate-950 transition-colors duration-300 w-full overflow-hidden">
      {/* Switch de tema en la esquina superior derecha */}
      <div className="absolute left-4 sm:left-8 top-4 sm:top-8 flex items-center gap-2">
        <Switch checked={isDark} onCheckedChange={toggleTheme} />
        <span className="text-sm text-slate-600 dark:text-slate-400">
          {isDark ? "Modo oscuro" : "Modo claro"}
        </span>
      </div>

      {/* Layout responsive: columnas en desktop grande (>1580px), filas en mobile y tablet */}
      <div className="w-full grid grid-cols-1 2xl:grid-cols-2">
        {/* Columna izquierda - Input */}
        <InputSection
          text={text}
          setText={setText}
          isLoading={isLoading}
          onSubmit={handleSubmit}
        />

        {/* Columna derecha - Resumen */}
        <SummarySection response={response} />
      </div>
    </main>
  );
}
