import { Check } from "lucide-react";

interface SummarySectionProps {
  response: string;
}

export function SummarySection({ response }: SummarySectionProps) {
  // Function to process the response and extract HTML if it's inside code blocks
  const processResponse = (text: string) => {
    // Check if the text contains HTML code blocks
    const htmlCodeBlockRegex = /```html\s+([\s\S]*?)```/g;
    const match = htmlCodeBlockRegex.exec(text);

    // If an HTML code block is found, extract only the HTML content
    if (match && match[1]) {
      return match[1].trim();
    }

    // Otherwise, return the original text
    return text;
  };

  const processedResponse = response ? processResponse(response) : "";

  return (
    <div className="w-full max-w-[50vw] min-h-[50vh] 2xl:min-h-screen bg-slate-100 dark:bg-slate-900 p-12">
      {response ? (
        <div className="h-full">
          <h2 className="text-lg sm:text-xl md:text-2xl font-semibold mb-4 text-slate-800 dark:text-slate-200 flex items-center">
            <Check className="mr-2 text-green-500" size={20} />
            Resumen Generado:
          </h2>
          <div className="text-slate-800 dark:text-slate-200 text-base sm:text-lg">
            <div
              dangerouslySetInnerHTML={{ __html: processedResponse }}
              className="whitespace-normal break-words text-justify"
            ></div>
          </div>
        </div>
      ) : (
        <div className="flex items-center justify-center h-full text-slate-400 p-4 sm:p-6">
          <p>El resumen aparecerá aquí</p>
        </div>
      )}
    </div>
  );
}
